.page{
  margin: 0 auto;
  padding: 0;
  min-width: 320px;
  min-height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 28px;
  font-weight: 400;
  color: $basic-black;
  background-color: $basic-white;
  box-sizing: border-box;
}

.page--js {
  @media (max-width: $tablet-width) {
    overflow: hidden;
  }
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
